.error {
  color: red;
  background-color: rgb(252, 197, 205);
  text-align: center;
  font-size: 20px;
  font-weight: bold;
  border-style: solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}

.success {
  color: rgb(8, 133, 8);
  background-color: rgb(170, 241, 170);
  text-align: center;
  font-size: 25px;
  font-weight: bold;
  border-style:solid;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}
